import React, { Component } from "react";
// import { withRouter } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardFooter,
  Col,
  Container,
  Form,
  // Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row
} from "reactstrap";
import { signUp } from "../../../services/HttpService"

import validate from "../../../validators/validate";
import confirmPassValidate from "../../../validators/confirmPassword";
import TextInput from "../../../Form/TextInput";
import PasswordInput from "../../../Form/PasswordInput";

class Register extends Component {

  constructor() {
    super();
    this.state = {
      isDanger : false,
      message : "",
      formIsValid: false,
      formControls: {
        username: {
          value: "",
          placeholder: "Username",
          valid: false,
          touched: false,
          validationRules: {
            isRequired: true
          }
        },
        email: {
          value: "",
          placeholder: "Email",
          valid: false,
          touched: false,
          validationRules: {
            isEmail: true
          }
        },
        password: {
          value: "",
          placeholder: "Password",
          valid: false,
          touched: false,
          validationRules: {
            isRequired: true
          }
        },
        repeatPassword: {
          value: "",
          placeholder: "Repeat password",
          valid: false,
          touched: false,
          validationRules: {
            isRequired: true
          }
        }
      }
    };
  }

  /**
   * input field change handler
   */
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const updatedControls = {
      ...this.state.formControls
    };
    const updatedFormElement = {
      ...updatedControls[name]
    };
    updatedFormElement.value = value;
    updatedFormElement.touched = true;
    updatedFormElement.valid = validate(
      value,
      updatedFormElement.validationRules
    );

    if (name === "repeatPassword" && updatedFormElement.valid)
      updatedFormElement.valid = confirmPassValidate(
        this.state.formControls.password.value,
        value
      );

    updatedControls[name] = updatedFormElement;

    let formIsValid = true;
    for (let inputIdentifier in updatedControls) {
      formIsValid = updatedControls[inputIdentifier].valid && formIsValid;
    }
    this.setState({
      formIsValid: formIsValid,
      formControls: updatedControls
    });
  };

  /**
   * submit button register
   */
  submitRegisterForm = async event => {
    event.preventDefault();
    const formData = {};
    for (let formElementId in this.state.formControls) {
      formData[formElementId] = this.state.formControls[formElementId].value;
    }

    // email,username, password and rpeat password
    console.dir(formData);
    // email and password
    try{
      delete formData['repeatPassword']
      let res = await signUp(formData)
      this.setState({
        isDanger : false
      })
      this.props.history.push('/login')
    } catch (e){
      console.log(e)
      this.setState({
        isDanger : true,
        message : e.Message
      })
    }
  };

  /**
   * httpError messsage show if error arise
  */
  httpError(){
    if (this.state.isDanger)
      return(
        <div className="alert alert-danger" role="alert">
          {this.state.message}
        </div>
      )
  }
 
  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardBody className="p-4">
                  <Form onSubmit={this.submitRegisterForm}>
                    <h1>Register</h1>
                    {this.httpError()}
                    <p className="text-muted">Create your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <TextInput
                        name="username"
                        placeholder={
                          this.state.formControls.username.placeholder
                        }
                        value={this.state.formControls.username.value}
                        onChange={this.changeHandler}
                        touched={
                          this.state.formControls.username.touched ? 1 : 0
                        }
                        valid={this.state.formControls.username.valid ? 1 : 0}
                      />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>@</InputGroupText>
                      </InputGroupAddon>
                      <TextInput
                        name="email"
                        placeholder={this.state.formControls.email.placeholder}
                        value={this.state.formControls.email.value}
                        onChange={this.changeHandler}
                        touched={this.state.formControls.email.touched ? 1 : 0}
                        valid={this.state.formControls.email.valid ? 1 : 0}
                      />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <PasswordInput
                        name="password"
                        placeholder={this.state.formControls.password.placeholder}
                        value={this.state.formControls.password.value}
                        onChange={this.changeHandler}
                        touched={this.state.formControls.password.touched ? 1 : 0}
                        valid={this.state.formControls.password.valid ? 1 : 0}
                      />
                    </InputGroup>
                    <InputGroup className="mb-4">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <PasswordInput
                        name="repeatPassword"
                        placeholder={this.state.formControls.repeatPassword.placeholder}
                        value={this.state.formControls.repeatPassword.value}
                        onChange={this.changeHandler}
                        touched={this.state.formControls.repeatPassword.touched ? 1 : 0}
                        valid={this.state.formControls.repeatPassword.valid ? 1 : 0}
                      />
                    </InputGroup>
                    <Button color="success" style={{cursor:"pointer"}} type="submit" disabled={!this.state.formIsValid} block>
                      Create Account
                    </Button>
                  </Form>
                </CardBody>
                <CardFooter className="p-4">
                <Row>
                  <div>
                    Click here for <span style={{color:"#89daf3", cursor:"pointer"}} onClick={this.props.history.goBack}>login</span>
                  </div>
                </Row>
                  {/* <Row>
                    <Col xs="12" sm="6">
                      <Button className="btn-facebook mb-1" block>
                        <span>facebook</span>
                      </Button>
                    </Col>
                    <Col xs="12" sm="6">
                      <Button className="btn-twitter mb-1" block>
                        <span>twitter</span>
                      </Button>
                    </Col>
                  </Row>
                  <Row> */}
                </CardFooter>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Register;
